@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);

body {
  margin: 0;
  font-family: 'Viaoda Libre', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Viaoda Libre', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: 'Viaoda Libre', cursive;
}


#top {
    width: 73%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#top-phone {
    display: flex;
    align-items: center;
}

#top-phone ion-icon {
    font-size: 1.3rem;
}

#top-phone p {
    font-size: 1.2rem;
}

#top-phone p {
    margin-top: 15px;
    margin-left: 5px;
}

#top-other ion-icon {
    margin: 0 5px;
    font-size: 1.3rem;
}

#top .hov:hover {
    cursor: pointer;
}

@media screen and (max-width: 1024px) {
    #top {
        width: 95%;
    }
}


#nav {
    background-color: #FFCD58;
}

#logo {
    font-size: 1.5rem;
    color: #000;
    text-decoration: none;
    margin-right: 2rem;
}

#home-link,
#menu-link,
#story-link,
#contact-link {
    color: #000;
}

#home-link:hover,
#menu-link:hover,
#story-link:hover,
#contact-link:hover,
#logo:hover {
    color: #4d4d4d;
}


#footer {
    background-color: #FFCD58;
}


#head {
    right: 0;
    width: 100%;
    min-height: 100vh;
    z-index: 2;
}

#head h2 {
    padding-top: 15%;
}

#head h1 {
    font-size: 5rem;
}

#top-button {
    background-color: #FFCD58;
    width: 150px;
    height: 40px;
    border: 0;
    font-weight: bold;
    text-transform: uppercase;
    margin-top: 20px;
}

#top-button:hover {
    background-color: #ffc233;
}

#head video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-top: 5rem;
    z-index: -1;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

/* SECOND_SECTION */

#second-section {
    align-items: center;
    margin: 5% auto;
}

#second-section p {
    font-size: 1.3rem;
}

#second-img img {
    width: 50%;
}

#story-button {
    -webkit-text-decoration-color: black;
            text-decoration-color: black;
    text-decoration-thickness: 1px;
}

#story-button h3 {
    color: black;
    font-size: 1.5rem;
}

/* MENU-SECTION */

#menu-section {
    background-color: #000;
}

#menu-section h2,
#menu-section h3,
#menu-section p {
    color: #ffffff;
}

#menu-section p {
    font-size: 1.2rem;
}

#arrow-right {
    color: #ffffff;
}

#menu-button {
    -webkit-text-decoration-color: #ffffff;
            text-decoration-color: #ffffff;
    text-decoration-thickness: 1px;
}

/* FOOD SECTION */

#food-card {
    display: flex;
    flex-wrap: wrap;
}

.food-div {
    width: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.food-div p {
    position: absolute;
    width: 80%;
    color: #fff;
    top: 10%;
    font-size: 4rem;
    text-align: center;
}

.food-div img {
    width: 100%;
    height: auto;
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
}

/* MEDIA */

@media screen and (min-width: 2500px) {
    #head h2 {
        padding-top: 17%;
        font-size: 4rem;
    }

    #head h1 {
        font-size: 6rem;
    }

    #top-button {
        width: 250px;
        height: 70px;
        margin-top: 40px;
        font-size: 1.7rem;
    }
}

@media screen and (max-width: 1024px) {
    #head h1 {
        font-size: 2.5rem;
    }

    #head h2 {
        font-size: 2rem;
    }

    #head > div {
        padding-top: 15%;
    }

    #head video {
        display: none;
    }

    #head {
        background-image: -webkit-gradinet(linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6))), url(/korlake-restaurant/static/media/home.f5b3148e.jpg);
        background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url(/korlake-restaurant/static/media/home.f5b3148e.jpg);
        background-size: cover;
        background-position: center;
        min-height: 100vh;
        min-width: 100wh;
    }

    #second-section {
        margin-top: 2rem;
        margin-bottom: 4rem;
    }

    #second-img img {
        width: 100%;
    }

    .food-div {
        width: 100%;
    }
}

@media screen and (max-width: 600px) {
    #head > div {
        padding-top: 50%;
    }
}


#head-title {
    background-color: #262626;
    padding-top: 1%;
    padding-bottom: 1%;
}

#head-title h1 {
    color: white;
    font-size: 3rem;
    padding: 3rem 0;
}

#breakfast {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/breakfast-background.996fa0f5.jpg);
}

#lunch {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/lunch-background.d643cefd.jpg);
}

#dinner {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/dinner-background.0eb19e38.jpg);
}

#dessert {
    background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url(/korlake-restaurant/static/media/dessert-background.66a3f9fd.jpg);
}

#breakfast,
#lunch,
#dinner,
#dessert {
    background-size: cover;
    background-position: center;
    min-height: 100vh;
}

.background-product {
    background-color: #FFCD58;
}

.menu-title {
    background-color: #FFCD58;
}

.menu-title h2 {
    margin-bottom: 0;
    font-size: 2.5rem;
}

#background-menu-title {
    background-color: #262626;
}


.product {
    padding-top: 60%;
}

.product h3,
.description {
    color: #fff;
}

.product h3 {
    font-size: 2rem;
}

.product p {
    font-size: 1.3rem;
}

.price {
    color: red;
}

@media screen and (max-width: 600px) {
    .product {
        padding-top: 20%;
    }
}


#allstory h1 {
    padding: 5%;
}

#allstory h3 {
    font-size: 2rem;
    margin-bottom: 2rem;
}

#food {
    display: flex;
    justify-content: space-around;
    margin-top: 4%;
    flex-wrap: wrap;
}

#food img {
    width: 33.3%;
    -webkit-filter: brightness(75%);
            filter: brightness(75%);
}

#food img:hover {
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
}

#header-img {
    width: 100%;
}

#paragraphs p {
    font-size: 1.5rem;
}

#stage {
    width: 50%;
    margin: 5rem 0;
}

@media screen and (max-width: 600px) {

    #stage {
        width: 100%;
        margin: 3rem 0;
    }

    #food {
        margin-top: 3rem;
    }

    #food img {
        width: 100%;
    }
}


#allcontacts h1 {
    padding: 5%;
}

#phone img {
    width: 100%;
    margin-top: 5%;
}

#allcontacts ion-icon {
    font-size: 2rem;
}

#allcontacts p {
    font-size: 1.3rem;
}

#allcontacts .hov:hover {
    cursor: pointer;
}

